<template>
	<view class="container">
		<view class="wrap">
			<!-- <u-swiper :list="list" mode="dot" :effect3d="true"></u-swiper> -->
			<swiper class="swiper" circular indicator-dots="true" autoplay="true" interval="5000"
				indicator-active-color="#fff" indicator-color="#d1d1d1" duration="800">
				<swiper-item>
					<view class="">
						<image src="../../static/swiper1.jpg" mode="" style="height: 300upx;width: 100%;"></image>
					</view>
				</swiper-item>
				<swiper-item>
					<view class="">
						<image src="../../static/swiper2.jpg" mode="" style="height: 300upx;width: 100%;"></image>
					</view>
				</swiper-item>
				<swiper-item>
					<view class="">
						<image src="../../static/swiper3.jpg" mode="" style="height: 300upx;width: 100%;"></image>
					</view>
				</swiper-item>
				<swiper-item>
					<view class="">
						<image src="../../static/swiper4.jpg" mode="" style="height: 300upx;width: 100%;"></image>
					</view>
				</swiper-item>
			</swiper>
		</view>
		<view class="tip">
			<text style="float: left;font-weight: bold;">资讯\n公告</text>
			<view class="tips">
			      <u-notice-bar 
				  direction="column" 
				  :text="text1" 
				  color="black" 
				  bgColor="rgba(220, 38, 38, 1);"
				  font-size="20upx"
				  ></u-notice-bar>
			</view>
		</view>
		<view class="salle">
			<a @click="gosale">
				<image src="../../static/912185043.png" mode=""></image>
			</a>
		</view>
		<view class="selectgoods">
			<u-tabs :list="list" 
			:is-scroll="false" 
			:current="currents" @change="change"></u-tabs>
		</view>
		<scroll-view class="scroll" scroll-y="true" v-if="currents==0">
			<box></box>
		</scroll-view>
		<scroll-view class="scroll" scroll-y="true" v-if="currents==1">
			<blodbox></blodbox>
		</scroll-view>
		<scroll-view class="scroll" scroll-y="true" v-if="currents==2">
			<active></active>
		</scroll-view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				currents: 0,
				text1:[
					'【十八数藏上新公告】民族风俗画系列藏品《秋收》明日上线',
					'【十八数藏活动公告】《韩熙载夜宴图N卡》抽检活动今日上线',
					'【十八数藏寄售公告】二期盲盒《韩熙载夜宴图》系列藏品开放寄售'
					],
				list: [{
					name: '数字藏品'
				}, {
					name: '盲盒'
				}, {
					name: '活动精选'
				}],
				goods:{}
			}
		},
		// mounted() {
		// 	this.load()
		// },
		methods: {
			async load() {
				let result = await this.$request({
					url: '/api/getgoods',
				})
				console.log(result);
				this.goods=result.data.message
			},
			change(index) {
				this.currents = index.index;
			},
			gologin() {
				uni.navigateTo({
					url: '/pages/login/ligin'
				})
			},
			gosale(){
				uni.switchTab({
					url:'/pages/sale/sale'
				}) 
			}

		},
		computed:{
			// scroll(){
			// 	return this.currents==1?'true':'false'
			// }
		},
		onLoad() {
			this.load()
		}
	}
</script>

<style>
	.container {
		height: auto;
	}
	.scroll{
		margin: 35upx;
		overflow: hidden;
	}
	.wrap {
		/* padding: 10px; */
		margin-left: 5%;
		width: 90%;
		height: 300upx;
		/* background-color: #a7b4ff; */
		border-radius: 50upx;
		overflow: hidden;
	}

	.tip {
		/* padding: 10px; */
		margin-left: 5%;
		width: 85%;
		height: 100upx;
		/* background-color: #eca6ff; */
		background-image: linear-gradient(-225deg, #FFFEFF 0%, #D7FFFE 100%);
		border-radius: 20upx;
		margin-top: 10upx;
		/* overflow: hidden; */
		padding-left: 30upx;
		border: 1upx solid #a7b4ff;
	}

	.tips {
		width: 90%;
		height: 100upx;
		padding-top: 10upx;
		border-radius: 40upx;
		/* background-image: linear-gradient(-225deg, #FFFEFF 0%, #D7FFFE 100%); */
	}

	.salle {
		/* padding: 10px; */
		margin-left: 5%;
		width: 90%;
		height: 180upx;
		/* background-color: #ffb8d1; */
		border-radius: 50upx;
		margin-top: 10upx;
	}
	.salle image{
		width: 100%;
		height: 180upx;
	}
	.selectgoods {
		position: sticky;
		top: 40px;
		/* padding: 10px; */
		margin-left: 4%;
		width: 100%;
		background-color: #fff;
		height: 100upx;
		/* background-color: #f2ff98; */
		/* border-radius: 50upx; */
		margin-top: 10upx;
		z-index: 1;
	}
</style>
